.project__list_item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  /* height: 50px; */

  height: 300px;
  width: 100%;

  /* margin: 0px 0.75em 2.25em; */
  /* margin: 0px 0em 2.25em; */
  margin: 0px 0px 40px;
  padding: 0;
  /* display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(auto, 1fr);
  row-gap: 10px;
  column-gap: 50px; */
  /* font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; */
  /* font-style: normal;
  font-size: 1em;
  font-weight: normal; */

  font-style: inherit;
  font-size: inherit;
  font-weight: inherit;
  color: #2c2b2b;

  /* background-color: #f4f4f4; */
  background-color: white;
  /* min-height:fit-content; */
  /* width: ; */

  /* padding: 1em 0px; */
  /* border-radius: 1em; */
  border-radius: 16px;
  /* border: 1px solid blue; */
  z-index: 5;
}

.project__list_item:hover {
  transform: scale(1);
  box-shadow: 0 0 5px 5px rgba(34, 60, 80, 0.2);
  transition: box-shadow 0.5s, transform 0.5s;
}

.project__list_item:last-child {
  /* margin-bottom: calc(2.25em + 40px); */
  /* margin-bottom: calc(40px + 40px); */
    margin-bottom: 0px;
}

.project__list_item:nth-child(odd) {
  align-self: center;
}

.project__list_item:nth-child(odd) h2 {
  /* margin-left: 2em; */
  margin-left: 60px;
  /* margin-right: 0.5em; */
  margin-right: 15px;
  text-align: end;
  }

.project__list_item:nth-child(even) {
  align-self: center;
  flex-direction: row-reverse;
}

.project__list_item:nth-child(even) h2 {
/* margin-right: 2em; */
margin-right: 60px;
/* margin-left: 0.5em; */
margin-left: 15px;
text-align: start;
}

