@media all and (min-width: 320px) and (max-width: 767px) {
  .project__list_item {

    flex-direction: column;
    /* align-items: center; */
    justify-content: flex-start;
    height: fit-content;
    width: 100%;

    margin: 0px 0em 40px;
    padding: 0;

    font-style: inherit;
    font-size: inherit;
    font-weight: inherit;
    color: #2c2b2b;

    background-color: white;
    /* min-height:fit-content; */
    /* width: ; */

    /* padding: 1em 0px; */
    border-radius: 16px;
    /* border: 1px solid blue; */
  }

  .project__list_item:nth-child(odd) {
    align-self: center;
  }

  .project__list_item:nth-child(odd) h2 {
    /* margin-left: 2em; */
    margin-left: 0;
    /* margin-right: 0.5em; */
    margin-right: 0;
    text-align: center;
    }

  .project__list_item:nth-child(even) {
    align-self: center;
    flex-direction: column;
  }

  .project__list_item:nth-child(even) h2 {
    /* margin-right: 2em; */
    margin-right: 0;
    /* margin-left: 0.5em; */
    margin-left: 0;
    text-align: center;
  }
}
