.stack__list_item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  /* display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(auto, 1fr);
  row-gap: 10px;
  column-gap: 50px; */
  /* font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; */
  /* font-style: normal;
  font-size: 1em;
  font-weight: normal; */

  font-style: inherit;
  font-size: inherit;
  font-weight: inherit;
  color: inherit;

  min-height:fit-content;
  /* width: ; */

  /* padding: 0.5em 0.5em; */
  padding: 10px 10px;
  background-color: white;
  /* border-radius: 0.75em; */
  border-radius: 16px;

  /* border: 1px solid blue; */
}

.stack__list_item:last-child {
  justify-content: center;
  align-items:flex-end;
}

.stack__list_item:last-child img {
  display: none;
}

.stack__list_item:last-child p {
  text-align: left;
  width: 5.5em;
  margin:0;
  border-top: none;
}

@media all and (min-width: 768px) and (max-width: 1023px) {
  .stack__list_item {

  }

  .stack__list_item:last-child {
    justify-content: center;
    align-items:center;
    /* height: 118px; */
  }

  .stack__list_item:last-child img {
    display: none;
  }

  .stack__list_item:last-child p {
    text-align: center;
    width: 6.5em;
    /* margin:0; */
  }
}

@media all and (min-width: 320px) and (max-width: 767px) {
  .stack__list_item {

  }

  .stack__list_item:last-child {
    justify-content: center;
    align-items:center;
    height: 118px;
  }

  .stack__list_item:last-child img {
    display: none;
  }

  .stack__list_item:last-child p {
